<template>
  <div class="page" id="activityPage">
    <div class="content">
      <div class="imgbox" v-lazy:background-image="activity.image_url"></div>
      <div class="bgc-white pt5 pb5">
        <div class="act-title">{{ activity.name }}</div>
        <div class="box pr pl15 pr15 pt5 pb5">
          <img v-lazy="activity.user_image" src="../assets/images/50.png" width=36 height=36 alt=""
               class="avatar">
          <div>
            <div class="name_overflow">{{ activity.admin_name }}</div>
            <div class="gray">{{ activity.create_at }}</div>
          </div>
          <div class="bf1 tr">
            <div class="gray">{{ activity.z_name }}</div>
            <div class="theme-color">{{ activity.limit_number }}({{ activity.participate_number }})</div>
          </div>
        </div>
      </div>
      <van-cell-group>
        <van-cell :title="activity.start_date">
          <template slot="icon">
            <img src="../assets/images/calaner.png" alt="" width="23" height="20">
          </template>
        </van-cell>
        <van-cell :title="activity.place">
          <template slot="icon">
            <img src="../assets/images/location.png" alt="" width="23" height="20">
          </template>
        </van-cell>
        <van-cell :title="signDetail" is-link @click="toDetail" v-if="activity.activity_type != 1">
          <template slot="icon">
            <img src="../assets/images/user.png" alt="" width="23" height="20">
          </template>
        </van-cell>
      </van-cell-group>
      <div class="intro" v-if="activity.introduce" v-html="activity.introduce"></div>
      <div class="intro" v-if="activity.activity_type == 3  && activity.identity_limit == 1"  style="margin-top: 0">&nbsp;</div>
    </div>
    <div class="btn-wrap box"  v-if="fromApp && fromParty && activity.activity_type == 3"><!--  //  123  -->
      <template v-if="activity.if_sign_count  == 0">
        <a @click="toSignPage" class="btn btn-success btn-round bf1 f17"></a>
      </template>
      <template v-if="activity.if_sign_count  == 1">
        <a @click="cancelAct" class="btn btn-success btn-round bf1 f17"></a>
      </template>
    </div>
     <div class="btn-wrap box"  v-if="!fromApp">
      <a @click="wakeApp(activity.advert_list_url)" class="btn btn-success btn-round f17" v-if="activity.order == 3">APP</a>
      <a @click="wakeApp(activity.advert_url)" class="btn btn-success btn-round bf1 f17" v-else>APP</a>
    </div>
    <div @click="innerWXopen=false">
      <WxOverlay v-show="innerWXopen"/>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'
  import WxOverlay from '@/components/WxOverlay.vue'
  import {wxShare} from "@/common/wxShare";
  export default {
    name: 'activity',
    data() {
      return {
        signDetail: '',
        signDetailLen: 0,
        unionact_id: null,
        activity: {},
        fromApp: true,
        isAndroid: utils.isAndroid(),
        isIOS: utils.isApple(),
        isWX: utils.isWeixin(),
        innerWXopen: false,
        timeId: null,

      }
    },
    created() {
      this.fromApp = utils.fromApp()
      this.unionact_id = utils.getQueryString(window.location.href, 'id')
      this.fromParty = utils.getQueryString(window.location.href, '_allowShare') || 0;
      this.pullData()
      this.getSigninfo()
    },
    components: {
      WxOverlay
    },
    methods: {
      wakeApp(url) {
        var href
        if (!url) {
          return
        }
        if (this.isWX) {
          this.innerWXopen = true
          return
        }
        if (this.isAndroid) {
          href = this.apiConfig.downloadUrl.android
        } else if (this.isIOS) {
          href = this.apiConfig.downloadUrl.ios
        }
        window.location.href = url
        this.timeId = setTimeout(() => {
          if (href) {
            window.location.href = href
          }
        }, 2500)
        document.addEventListener('visibilitychange', () => {
          var isHidden = document.hidden
          if (isHidden) {
            clearTimeout(this.timeId)
          }
        })
      },
      toDetail() {
        if (!this.fromApp) {
          return
        }
        let unionact_id = this.unionact_id + ''
        if (this.isAndroid) {
          window.jsInterface.activityRegister(unionact_id)
        } else if (this.isIOS) {
          window.webkit.messageHandlers.activityRegister.postMessage(unionact_id)
        }
      },
      toSignPage() {
        if (!this.fromApp) {
          return
        }
        if(this.activity.limit_number <= this.activity.participate_number) {
              this.$toast({message:'', duration: 2000});
          return false;
        }
         if(this.activity.identity_limit == 1 &&  this.activity.activity_type == 3) {  //
            this.getPartyInfo();
         }
        let unionact_id = this.unionact_id + ''
        if (this.isAndroid) {
          window.jsInterface.joinActivity(unionact_id)
        } else if (this.isIOS) {
          window.webkit.messageHandlers.joinActivity.postMessage(unionact_id)
        }
      },
      cancelAct() {  // 

         this.axios.post(this.apiConfig.cancelAct, {unionact_id: this.unionact_id}).then(res => {
           if (res.s !== 1) {
              this.$toast({message:res.m, duration: 2000});
              return false;
           }
          this.$toast({message:'', duration: 2000});
          this.pullData();

         })


      },
      getSigninfo() {
        if (!this.fromApp) {
          return
        }
        this.axios.post(this.apiConfig.signinfo, {unionact_id: this.unionact_id, page: 1, page_num: 15}).then(res => {
          if (res.s !== 1) {
            this.$toast(res.m)
            setTimeout(() => {
              this.$router.push({
                name: 'Index'
              })
            }, 1000)
            return
          }

          this.signDetailLen = res.d.total
          if (this.signDetailLen > 0) {
            this.signDetail = '(' + this.signDetailLen + ')'
          } else {
            this.signDetail = ''
          }
        })
      },
      pullData() {
        this.axios.post(this.apiConfig.activity, {unionact_id: this.unionact_id}).then(res => {
          if (res.s !== 1) {
            this.$toast(res.m)
            setTimeout(() => {
              this.$router.push({
                name: 'Index'
              })
            }, 1000)
            return
          }
          this.activity = res.d;
          this.$store && this.$store.dispatch('UPDATE_LOADINGSTATE', false)
          let desc = ''
          wxShare({
            title: res.d.name,
            desc,
            link: window.location.href,
            imgUrl: res.d.op_spic,
            success: function () {
            }
          })
        })
      },
      getPartyInfo() {

         this.axios.get(this.apiConfig.getPartyInfo).then(res => {
           if (res.s !== 1) {
              this.$toast({message:'', duration: 2000});
              return false;
           }
         })
      }
    },

  }
</script>
<style scoped lang="scss">
  @import '../assets/scss/Common/variables.scss';
  .imgbox {
    background: url(../assets/images/500.png) no-repeat center;
    background-size: cover;
    height: rem(165px);
    width: 100%;
  }
  .name_overflow {
    max-width: 40vw;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .act-title {
    padding: 5px rem(15px);
    margin-top: 2px;
    font-size: rem(16px);
    font-weight: 700;
  }

  .avatar {
    margin: 3px 10px 10px 0;
    border-radius: 50%;
  }

  .intro {
    margin-top: 5px;
    background-color: white;
    padding: rem(10px) rem(15px);
    font-size: rem(15px);
  }
</style>
<style lang="scss">
  @import '../assets/scss/Common/variables.scss';
  #activityPage .van-cell__title {
    margin-left: rem(15px);
    font-size: rem(16px);
  }

  #activityPage .van-cell {
    padding: rem(14px) rem(15px);
    margin-left: 0;
  }

  #activityPage .van-cell img {
    margin-left: rem(6px);
  }
</style>
